@import url(https://fonts.googleapis.com/css2?family=Chivo:wght@300&family=Noto+Sans+HK:wght@300&family=Playfair+Display:ital,wght@1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Chivo:wght@300&family=Noto+Sans+HK:wght@300&family=Playfair+Display:ital,wght@1,800&display=swap);
.ui.segment {
    padding: 3em;
    margin: 4em;
    border-radius: 20px;
    box-shadow: 0 10px 40px #ff7c7c, 0 0 0 20px #ffffffeb;
    background-color: #ff7575;
};


.image-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 0 10px;
    gap: 0 10px;
    grid-auto-rows: 10px;
}

.image-list img {
    width: 250px;
    grid-row-end: span 2;
}

header {
    font-size: 3.5rem;
    margin-bottom: 50px;
    text-align: center;
    font-family: 'Playfair Display', serif;
    font-weight: 800;
    font-style: italic;
    color: white;
}

#input {
    font-family: 'Noto Sans HK', sans-serif;
    background: #ffffffeb;
    border-radius: 10px;
}

body {
    background-color: #ffd8d8;
}
